.overlay{
    background: #000000e3;
    position: fixed;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    z-index: 999999999;
}
.popupContainer{
    width: 50%;
    background: white;
    position: fixed;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 1rem 2rem;
}

.popupContainer>p:nth-child(1){
    font-size: 1.5rem;
}

.popupContainer>p{
    letter-spacing: 0.03rem;
    padding-bottom: 1rem;
}

.popupBtn{
    border: 1px solid black;
    background: white;
    padding: 0.5rem 0.8rem;
    margin: 0 0.5rem;
    outline: none;
    cursor: pointer;
    transition: all 0.2s ease;
}

.popupBtn:hover{
    color: white;
    background:black;
    border: 1px solid greenyellow;
}

.popupBtn:nth-child(2):hover{
    color: white;
    background:black;
    border: 1px solid red;
    
}