.joinBtn{
  display: inline-block;
  padding: 0.8rem;
  border: 1px solid white;
  margin: 1rem 0;
  width: 9rem;
  text-align: center;
  font-size: 1.2rem;
  cursor: pointer;
  color: white;
  text-decoration: none;
  transition: all 0.2s ease;

}

.joinBtn:hover{
  background: white;
  color: black;
}
.sliderContainer{
    width: 100%;
    height: 100%;
    overflow: hidden;
    background: #000000ad;
}
  
.sliderContainer>ul{
    width:400%;
    height:100%;
    display:flex;
    animation:move 10s ease infinite;
}

.sliderContainer>ul:hover {
    animation-play-state: paused;
}
  
.sliderContainer li{
  width: 25%;
  height: 100%;
  list-style: none;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 1.8rem;
  text-align: center;
}
  
.sliderContainer span{
    width: 80%;
}
  
@keyframes move{
    0%{
      transform:translateX(0);
    }
    20%{
      transform:translateX(0);
    }
    25%{
      transform:translateX(-25%);
    }
    45%{
      transform:translateX(-25%);
    }
    50%{
      transform:translateX(-50%);
    }
    70%{
      transform:translateX(-50%);
    }
    75%{
      transform:translateX(-75%);
    }
    95%{
      transform:translateX(-75%);
    }
    100%{
      transform:translateX(0);
    }
}

