@font-face {
  font-family: 'Raleway Regular';
  font-style: normal;
  font-weight: normal;
  src: local('Raleway Regular'), url('./fonts/raleway/Raleway-Light.woff') format('woff');
}

*{
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Raleway Regular';
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.banner{
  display: flex;
  justify-content: center;
  align-items: center;
  height: 5rem;
  background: #C4C4C4;
  margin: 0 5rem;
}

.banner>p{
  font-size: 3rem;
  letter-spacing: 0.3rem;
}

.marTop{
  margin-top: 6rem;
}

.whiteTxt{
  color: white;
}

.imgMain{
  background-image: url("./imgs/books.jpg");
}

.imgServices{
  background-image: url("./imgs/services.jpg");
}

.imgAbout{
  background-image: url("./imgs/about.jpg");
}

