.mainContainer{
    background-color: #ccc;
    width: 100%;
    height: 35rem;
    display: flex;
    align-items: center;
    justify-content: space-around;
    background-position: center;
    background-size: cover;
}

.mainText{
    width: 80%;
    height: 60%;
    box-sizing: border-box;
    color: white;
    font-size: 2.5rem;
    letter-spacing: 0.3rem;

}

.mainImage{
    width: 20%;
    height: 80%;
    background: #ffffffad;
    display: flex;
    align-items: center;
    justify-content: center;
}

.mainImage>img{
    width: 65%;
}