.abContainer{
    padding: 2rem 5rem;
    display: flex;
}

.abtImg{
    width: 50%;
    background-position: center;
    background-size: cover;
    opacity: 0.9;
}
.abWrapper{
    display: flex;
    flex-direction: column;
    width: 50%;
    height: auto;
    padding: 2rem 3rem;
}

.abCol{
    width: 100%;
    height: 100%;
}

.abCol:nth-child(2){
    margin-top: 2rem;
}