.hText{
    font-size: 3rem;
    letter-spacing: 0.3rem;
    margin-bottom: 1.5rem;
}

.wwaWrapper{
    display: flex;
    flex-direction: row;
    width: 100%;
    height: auto;
    padding: 5rem 10rem;
}

.wwaCol{
    width: 50%;
    height: 100%;
    padding: 0 3rem;
}

.valuesContainer{
    list-style: none;
    padding-top: 0.8rem;
}

.pText{
    line-height: 1.5rem;
    text-align: justify;
}

.valuesContainer>li{
    padding-bottom: 0.8rem;
    line-height: 1.5rem;
    text-align: justify;
}

.valuesContainer>li>span{
    font-weight: bold;
}